import Vue from 'vue';
import App from './App.vue';
import VueRouter from 'vue-router';
import VueScroller from 'vue-scroller';
import VeeValidate, { Validator } from 'vee-validate';
import zh_CN from 'vee-validate/dist/locale/zh_CN';
import routes from './router.config';
import store from './store/'
import axios from 'axios';
import Mint from 'mint-ui';
import 'mint-ui/lib/style.css';
import VueHtml5Editor from 'vue-html5-editor'
import Font from 'font-awesome-webpack';
import Loading from './components/loading/loading';
import $ from 'jquery';
Vue.use(VueRouter);
VueRouter.prototype.goBack = function () {
  this.isBack = true;
  window.history.go(-1);
};
Vue.use(Mint);
Vue.use(Loading);
Vue.use(VueScroller);
// Vue.use(VueQuillEditor);
Vue.use(VueHtml5Editor, {
  // new VueHtml5Editor(options)
  // global component name
  name: "editor",
  // 
  // if set true,will append module name to toolbar after icon
  showModuleName: true,
  // classfont-awesome
  // custom icon class of built-in modules,default using font-awesome
  icons: {
    text: "fa fa-pencil",
    color: "fa fa-paint-brush",
    font: "fa fa-font",
    align: "fa fa-align-justify",
    list: "fa fa-list",
    link: "fa fa-chain",
    unlink: "fa fa-chain-broken",
    tabulation: "fa fa-table",
    image: "fa fa-file-image-o",
    hr: "fa fa-minus",
    eraser: "fa fa-eraser",
    undo: "fa-undo fa",
    "full-screen": "fa fa-arrows-alt",
    info: "fa fa-info",
  },
  // 
  // config image module
  image: {
    //   max file size
    sizeLimit: 512 * 1024*10000,//()
    // ,base64
    // upload config,default null and convert image to base64
    upload: {
      url: null,
      headers: {},
      params: {},
      fieldName: {}
    },
    // ,localResizeIMG,null
    // compression config,default resize image by localResizeIMG (https://github.com/think2011/localResizeIMG)
    // set null to disable compression
    compress: {
      width: 1600,
      height: 1600,
      quality: 80
    },
    // ,
    // handle response datareturn image url
    uploadHandler(responseText){
      //default accept json data like  {ok:false,msg:"unexpected"} or {ok:true,data:"image url"}
      var json = JSON.parse(responseText);
      if (!json.ok) {
        alert(json.msg)
      } else {
        return json.data
      }
    }
  },
  // en-uszh-cn
  //default en-us, en-us and zh-cn are built-in
  language: "zh-cn",
  // 
  i18n: {
    //specify your language here
    "zh-cn": {
      "align": "",
      "image": "",
      "list": "",
      "link": "",
      "unlink": "",
      "table": "",
      "font": "",
      "full screen": "",
      "text": "",
      "eraser": "",
      "info": "",
      "color": "",
      "please enter a url": "",
      "create link": "",
      "bold": "",
      "italic": "",
      "underline": "",
      "strike through": "",
      "subscript": "",
      "superscript": "",
      "heading": "",
      "font name": "",
      "font size": "",
      "left justify": "",
      "center justify": "",
      "right justify": "",
      "ordered list": "",
      "unordered list": "",
      "fore color": "",
      "background color": "",
      "row count": "",
      "column count": "",
      "save": "",
      "upload": "",
      "progress": "",
      "unknown": "",
      "please wait": "",
      "error": "",
      "abort": "",
      "reset": ""
    }
  },
  // 
  // the modules you don't want
  hiddenModules: [
    "list",
    "full-screen",
    "tabulation",
    "info",
  ],
  // 
  // keep only the modules you want and customize the order.
  // can be used with hiddenModules together
  visibleModules: [
    // "text",
    // "color",
    "font",
    // "align",
    "list",
    // "link",
    // "unlink",
    // "tabulation",
    "image",
    // "hr",
    // "eraser",
    // "undo",
    // "full-screen",
    // "info",
  ],
});
//VeeValidate,
Validator.addLocale(zh_CN);
//
const config = {
    errorBagName: 'errors', // change if property conflicts.
    fieldsBagName: 'fields',
    delay: 0,
    locale: 'zh_CN',
    dictionary: null,
    strict: true,
    enableAutoClasses: false,
    events: 'blur',//
};
Vue.use(VeeValidate,config);
/*Vue.prototype.$$ = zepto;*/
//config routes
const router = new VueRouter({
    mode: 'hash',
    // mode: 'history',
    base: '/mobile/',
    scorllBehavior: () => ({
        y: 0
    }),
    routes
});
// vuex
if (window.localStorage.getItem('token')) {
    store.dispatch('setToken',window.localStorage.getItem('token'));
}
// http request 
axios.interceptors.request.use(
    config => {
        //token
        if (window.localStorage.getItem('token')) {
            config.headers.Authorization = ` JWT ${window.localStorage.getItem('token')}`;
        }
        store.dispatch('showLoading');
        return config;
    },
    err => {
        return Promise.reject(err);
    }
);
axios.interceptors.response.use(
  response => {
    store.dispatch('hideLoading');
    return response;
  },
  error => {
    if (error.response) {
      switch (error.response.status) {
        case 401:
          // 401 token
          store.dispatch('clearToken');
          store.dispatch('clearUserId');
          router.replace({
            path: '/login',
            query: {redirect: router.currentRoute.fullPath}
          })
      }
    }
    return Promise.reject(error.response.data)
  });
axios.defaults.baseURL = 'http://m.ciyigou.com:80';
// axios.defaults.baseURL = 'http://dev.ectrend.com:80';
// axios.defaults.baseURL = 'http://192.168.0.200:8080';
axios.defaults.headers['Content-Type'] = 'application/json;charset=utf-8';
// 
router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        if (window.localStorage.getItem('token')) {
            next();
        } else {
            next({
                path: '/login',
                query: { redirect: to.fullPath }
            });
        }
    } else {
        next();
    }
});
//axiosVue
Vue.prototype.$http = axios;
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: { App }
});



